<template>
    <div class="" style="overflow: auto;" @scroll="scrollEvent">
        <div class="w90p p20" style="margin:0 auto;">
            <div class="f18 clof p5_0 ">交易所行情</div>
            <div class="secondMenus mt24 alignCenter">市场：
                <div v-for="(v,o) in areaList" :key="o" class="secondMenu" :class="{'active':active==o}" @click="active=o;search();">{{v.text}}</div>
                <!-- <div class="secondMenu" :class="{'active':active==0}" @click="active=0">全局概览</div>
                <div class="secondMenu" :class="{'active':active==1}" @click="active=1;$router.push('/exchange_quotation')">交易所行情</div>
                <div class="secondMenu" :class="{'active':active==2}" @click="active=2;;$router.push('/ccer_quotation')">全国CCER行情</div>
                <div class="secondMenu" :class="{'active':active==3}" @click="active=3;$router.push('/manager_quotation')">经纪商行情</div>
                <div class="secondMenu" :class="{'active':active==4}" @click="active=4;$router.push('/external_quotation')">国外碳价行情</div> -->
                <Button shape="circle" class="ml10 right" style="min-width:80px;" @click="isFilterPop=true">条件选品种</Button>
            </div>
            <table class="defineTable mt20 dataList " cellspacing="0">
                <thead>
                    <tr class="">
                        <!-- <td class="tleft">品种代码</td> -->
                        <td class="">#</td>
                        <td class="">品种名称</td>
                        <td class="controlBox">预警</td>
                        <td class="">品种类目</td>
                        <td>价格</td>
                        <td>开盘</td>
                        <td>收盘</td>
                        <td>涨跌%（日）</td>
                        <td>成交量（日）</td>
                        <td>成交额（日）</td>
                        <td>振幅</td>
                    </tr>
                </thead>
                <tbody>
                    <tr v-for="(v,o) in list" :key="o" :class="{'active':tableActive==o}" @click="tableActive=o" @dblclick="$router.push(`/trading?area=${v.area}`)">
                        <!-- <td class="tleft">FQ25865</td> -->
                        <td class="">{{ o+1 }}</td>
                        <td class="">{{ v.areaName }}</td>
                        <td class="controlBox">
                            <img :src="require('@/assets/img/warn.svg')" :title="'预警'" @click.stop="addWarn()" class="active_t" style="width:20px;height:20px;" />
                            <!-- <img :src="require('@/assets/img/warn_red.svg')" :title="'预警'" class="active_t" style="width:20px;height:20px;" /> -->
                        </td>
                        <td>二干醇</td>
                        <td :class="[$base.getColor(v.close,v.open)]">{{ v.price }}</td>
                        <td>{{ v.open }}</td>
                        <td :class="[$base.getColor(v.close,v.open)]">{{ v.close }}</td>
                        <td :class="[$base.getColor(v.close,v.open)]">
                            <span class="circleBox" :class="[$base.getColor(v.close,v.open)]">{{ v.increase*100 | toPercent(2,1) }}</span>
                        </td>
                        <td >{{ v.totalVol | numFormat(2) }}</td>
                        <td class="">{{ v.totalValue | numFormat(2) }}</td>
                        <td class="">1%</td>
                    </tr>
                    <tr v-if="list.length==0">
                        <td colspan="11" class="center cloc">暂无数据</td>
                    </tr>
                    <!-- <tr v-for="(v,o) in 10" :key="o" @click="toTrading()">
                        <td class="tleft">FQ25865-全国交易所TESTL</td>
                        <td>45.5</td>
                        <td>50.5</td>
                        <td>46.5</td>
                        <td class="green">-1.5%</td>
                        <td class="green">15000</td>
                        <td class="">877621.15</td>
                        <td class="">1%</td>
                    </tr> -->
                </tbody>
            </table>
        </div>
        <warnPop v-model="isWarnPop"></warnPop>
        <filterPop v-model="isFilterPop"></filterPop>
    </div>
</template>

<script>
import warnPop from '@/components/warnPop.vue'
import filterPop from '@/components/filterPop.vue'
export default {
    name: "",
    props: {},
    components: {
        warnPop,filterPop
    },
    data() {
        return {
            active:0,
            tableActive:-1,
            // areaList:this.$store.state.app.areaList,//区域列表
            areaList:[{
                type:'china',
                text:'国内',
            },{
                type:'eu',
                text:'国外',
            },{
                type:'other',
                text:'其他',
            }],//区域列表
            list:[],
            page:{
                "current": 1,
                "size": 20,
                // "size": 5,
                loading:false,
                finish:false,
            },
            isWarnPop:false,
            isFilterPop:false,
        };
    },
    mounted() {
        this.getList();
    },
    methods: {
        //添加预警
        addWarn(v,o){
            this.isWarnPop=true;
        },
        search(){
            this.page.current=1;
            this.getList();
        },
        // 滚定监听
        scrollEvent(e) {
            let that=this;
            if (Math.ceil(e.currentTarget.scrollTop + e.currentTarget.clientHeight) >=e.currentTarget.scrollHeight-20) {   //容差：20px
                console.log('滚动到底部');
                if(!that.page.finish && !that.page.loading){
                    that.page.current+=1;
                    that.getList();
                }
            }
        },
        //列表
        async getList(){
            if(this.page.loading){
                return;
            }
            // china：中国
            // other：国外
            // eu：其他
            let d={
                area: this.areaList[this.active].type,
                current: this.page.current,
                size: this.page.size,
            };
            this.page.loading=true;
            let res = await this.$api.market_page(d);
            let list = res.list || [];
            if (list.length >= d.size) {
                this.page.finish = false;
            } else {
                this.page.finish = true;
            }
            if(d.current==1){
                this.list=list;
            }else{
                this.list=this.list.concat(list);
            }
            this.page.loading=false;
        },
    },
};
</script>

<style scoped lang='less'>
.defineTable{
    background: #17171a;
}
.controlBox{
    background: #41414170 !important;
    text-align: center;
}
</style>